.registration-form {
	border-radius: 8px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    display: block;
    margin: 0 auto;
    width: 450px;
    padding: 60px 0px;
}
.registration-form form {
	width: 300px;
	margin: 0 auto;
}
.registration-form input {
	width: 100%;
	height: 28px;
    line-height: 2em;
    margin: 16px 0;
    font-size: 16px;
    border: none;
    border-bottom: 1px solid black;
}
.registration-form input:focus {
	outline: none;
	border-bottom: 3px solid #ff7149;
	height: 26px;
}
.submit-button {
	margin-top: 25px;
	border-radius: 25px;
	display: inline-block;
	padding: 11px 24px;
	color: #ff7149;
	font-weight: 700;
	font-size: 16px;
}
.submit-button:focus {
	outline: none;
}
.submit-button-orange {
	color: white;
	font-weight: 700;
	font-size: 16px;
	background-color: #ff7149;
	border: none;
}

#home-page {
	width: 80%;
	margin: 0 auto;
}
#home-title{
  color: black;
  text-align: center;
  background-color:	#D8D8D8;
  padding: 10px;
  margin-top: 0;
}
.home-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: 70px;
}
.subheader {
	font-weight: 700;
	color: #b5b5b5;
}
.single-pass {
	width: 45%;
	max-width: 350px;
	line-height: 1.5em;
	display: flex;
	margin: 20px 0;
	padding: 15px 30px;
	font-weight: 700;
	border-radius: 3px;
	border: 1px solid #ececec;
}
.single-pass:hover {
	border: 1px solid #ff7149;
}
.single-pass img {
	margin: 10px 20px 7px 14px;
}
.pass-info {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.pass-name {
	float: left;
	display: table;
	width: 70%;
}
.pass-name a {
	text-decoration: none;
	color: #ff7149;
	font-size: 20px;
}
.pass-price {
	font-weight: 500;
	font-color: grey;
	float: right;
	text-align: right;
}
.pass-num {
	font-weight: 200;
	padding-right: 10px;
	color: grey;
	font-size: 14px;
}
.pass-location {
	font-weight: 200;
	font-size: 14px;
}
.pass-button {
	margin-top: 10px;
}
.info-pass img {
	margin: 4px 20px 6px 14px;
}
.info-pass .pass-info {
	color: black;
	width: 100%;
	font-size: 20px;
	margin-top: 12px;
}

#emails{
  text-align: center;
}
#avg-price{
  text-align:center;
}
