}
#container{
  margin-left: 20px;
  width: 50%;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  word-break: break-all;
}
.section {
  width: 70vw;
  margin: 0 auto;
  padding-bottom: 90px;
}
.section-header {
  font-size: 26px;
  color: #ff7149;
  font-weight: 700;
  width: 100%;
  border-bottom: 1px solid #ececec;
  line-height: 2em;
}
.label {
  float: left;
  vertical-align: baseline;
  font-weight: 700;
  line-height: 3em;
}
.account-section {
  font-size: 20px;
  border-bottom: 1px solid #ececec;
}
.account-section input {
  height: 30px;
  padding: 5px;
  margin: 10px;
  border: none;
  font-size: 16px;
  width: 70%;
}
.account-section input:focus {
  outline: none;
}
.text {
  font-size: 20px;
  line-height: 1.7em;
}
.review-section {
	font-size: 16px;
}
.review-section .submit-button {
	margin: 0 10px;
	padding: 6px 12px;
}
.rating {
	padding: 15px 0 35px 0;
}
.single-review {
	padding: 20px 0;
}
.review-name {
	font-weight: 700;
}
.review-text {
	font-size: 20px;
	line-height: 2em;
}
#admin-page {
	width: 80%;
	margin: 0 auto;
}
.activity-table {
	padding: 20px 10px 30px 10px;
	margin: 60px 0;
}
#admin-page table {
	padding:30px 0;
	border-spacing: 0;
	width:100%;
	font-size: 14px;
}
#admin-page th {
	text-align: left;
	padding: 10px 20px;
	margin: 0;
	border-bottom: 2px solid #e9ecef;
}
#admin-page td {
	padding: 10px 20px;
	margin: 0;
	border-bottom: 1px solid #e9ecef;
}
#admin-page tr:last-child td{
	border-bottom: 0;
}
#admin-page .table-header {
	text-align: left;
	font-size: 16px;
	margin: 24px 20px 28px 20px;
	font-weight: bold;
	text-decoration: underline;
}
#admin-page .type {
	text-transform: capitalize;
	font-size: 11px;
	border-radius: 10rem;
	padding: 2px 10px;
	font-weight: bold;
	display: inline-block;
}
/* type colors */
.red {
	color: #d32f2f;
	background-color: #ffebee;
}

.yellow {
	color: #f57c00;
	background-color: #fff3e0;
}
.green {
	color: #388e3c;
	background-color: #e8f5e9;
}

/* style modal */
.modal {
	position: fixed;
    top: 50%;
    left: 50%;
    background-color: white;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 20px 30px;
    
}


